import React, { useState } from 'react';
import questions from './questions';

export default function App(props) {
  const [currentQuestion, setCurrentQuestion] = useState(0);
  const [score, SetScore] = useState(0);
  const [countScore, SetCountScore] = useState(false);

  const handleButtonClick = (isCorrect) => {
    const nextQuestion = currentQuestion + 1;
    setCurrentQuestion(nextQuestion);

    if (nextQuestion < props.value) {
      setCurrentQuestion(nextQuestion);      
    } else {
      SetCountScore(true);
    };

    if (isCorrect) {
      SetScore(score + 1);
    };
  };

	return (
		<div>
			{countScore ? (
				<div className='score-section'><p>You scored {score} out of {props.value}</p>
				<p className='score-tryagain'>
				<a href="/">TRY AGAIN</a>
				</p>
				</div>
			) : (
				<>
					<div className='question-section'>
						<div className='question-count'>
							<span>Question {currentQuestion}</span>/{props.value}
						</div>
						<div className='question-text'>{questions[currentQuestion].question}</div>
					</div>
					<div className='answer-section'>
						{questions[currentQuestion].answers.map(
						(answerOption, index)=> 
						<button 
							key={index} 
							onClick={()=> handleButtonClick(answerOption.isCorrect)}>{answerOption.answer}
						</button>
						)}
					</div>
				</>
			)}
		</div>
	);
}
